.Hero-Container{
    width: 100%;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 2fr 2fr;
    @media (max-width: 640px) {
        grid-template-columns:  1fr;
        grid-template-rows: 2fr 2fr;
    }
}

.Hero-Right{
    display: grid;
    justify-items: center;
    @media (max-width: 640px) {
       padding-top: 2rem;
    }
}

.img1{
    grid-column: 1/span 2;
    grid-row: 2/ span 2;
    border-radius: 45% 45% 0 0;
    z-index: 12;
    height: 17rem;
    @media (max-width: 640px) {
    grid-column: 2/span 2;
    }
}

.img2{
    grid-column: 2/ span 2;
    grid-row: 1 / span 2;
    height: 50rem;
    z-index: 1;
    object-fit: cover;
    @media (max-width: 640px) {
        height: 30rem;
        grid-column: 1/ span 5;
    }
}
.img3{
    grid-column: 3/span 3;
    grid-row: 1 / span 2;
    height: 15rem;
    margin-top: 2rem;
    z-index: 0;
    margin-left: 1rem;
}
        
.Hero-Left{
    display: grid;
    height: 50rem;    
    @media(max-width: 640px){height: 40rem}
    .row-1{
        font-family:'Times New Roman', Times, serif;
    }
    .row-2{
        @media(max-width: 640px){text-align: end;}
    }
    .row-3{
        font-family:'Times New Roman', Times, serif;
        @media(max-width: 640px){
        display: flex;
        flex-direction: column;}
    }
}
.socialContainer{
    position: relative;
    h1{     
        font-family: 'Playfair Display', serif;
    }
    .line-container{
        position: absolute;
       
    }
    .socialTitle{
       z-index: 10;
    }
    .socialText{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}

.services-Container{
    display: flex;
    justify-content: center;
    height: 10rem;
    position: relative;
    .services-text{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        h1{
            
            font-family: 'Playfair Display', serif;
        }
        }
    .sub-title{
        background-color: #F8E7DD;
        color: #E9995F;
        display: flex;
        justify-content: center;
        font-size: 1.5rem;
    }
}


.grid-container {
    columns: 5 200px;
    column-gap: 1.5rem;
    width: 90%;
    margin: 0 auto;
    div {
      width: 150px;
      margin: 0 1.5rem 1.5rem 0;
      @media(max-width:600px){margin:0 0 1.5rem 0}
      display: inline-block;
      width: 100%;
      border: solid 2px black;
      padding: 5px;
      box-shadow: 5px 5px 5px rgba(0,0,0,0.5);
      border-radius: 5px;
      transition: all .25s ease-in-out;
      &:hover img {
        filter: grayscale(0);
      }
      &:hover {
        border-color: coral;
      }
      img {
        width: 100%;
        filter: grayscale(100%);
        border-radius: 5px;
        transition: all .25s ease-in-out;
      }
      p {
        margin: 5px 0;
        padding: 0;
        text-align: center;
        font-style: italic;
      }
    }
  }
  